class help {

   check_name_if_is_empty(Name) { 
      return this.empty_name = !Name;
   }

   check_mark_if_is_empty(Mark) { 
      return this.empty_mark = !Mark;
   }

   check_Level_if_is_empty(Level) { 
      return this.empty_level = !Level;
   }

   check_Email_if_is_empty(Email) { 
      return this.empty_Email = !Email;
   }

    check_length_name(Name){
        const get_inserted_user_Name = Name;
        const give_format_inserted_user_Name = get_inserted_user_Name.toString().trim().toLowerCase();
        return this.weak_Name = (give_format_inserted_user_Name.length < 6);
        
    }
    
    check_if_name_contains_number(Name){
      const get_inserted_user_Name = Name;
      const give_format_inserted_user_Name = get_inserted_user_Name.toString().trim().toLowerCase();
      return this.weak_Name = (give_format_inserted_user_Name.search(/[0-9]/) === -1);
   }

   check_if_name_contains_character(Name){
      const get_inserted_user_Name = Name;
      const give_format_inserted_user_Name = get_inserted_user_Name.toString().trim().toLowerCase();
      return this.weak_Name = (give_format_inserted_user_Name.search(/[!\@\#\$\%\&\*\~\`\:\""\''\'\"\?\/\,\.]/) === -1);
   }

   check_if_the_use_sample(Name) {
      return this.sample = Name.toString().trim().toLowerCase() === "qwerty@123";
   }

   check_email(Email){
      const get_inserted_user_email = Email;
      const give_format_the_email_user_inserted_before_that_email_goes_in_the_database_to_save = get_inserted_user_email.toString().trim().toLowerCase();
      const mail = give_format_the_email_user_inserted_before_that_email_goes_in_the_database_to_save;
      const real_email = /^(([a-zA-Z0-9\.-_]{2,6})+)@(([a-zA-Z0-9]{2,6})+).([a-z]{2,6})(.[a-z]{2,6})$/;
      return this.finalCheck = (!real_email.test(mail));
   }
} 

export default new help();